@font-face {
    font-family: 'Barlow', sans-serif;
    font-family: 'Barlow Condensed', sans-serif;
    font-family: 'Bellefair', serif;
    src: url('https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@400;700&family=Bellefair&display=swap');
  }

  $ff-serif: "Bellefair",serif;
  $ff-sans-cond: "Barlow Condensed", sans-serif;
  $ff-sans-normal: "Barlow", sans-serif;

  $fs-900: 9.375rem;
  $fs-800: 6.25rem;
  $fs-700: 3.5rem;
  $fs-600: 2rem;
  $fs-500: 1.75rem;
  $fs-400: 1.125rem;    
  $fs-300: 1rem;
  $fs-200: 0.875rem;    

  $clr-dark: 230 35% 7%;
  $clr-light: 231 77% 90%;
  $clr-white: 0 0% 100%;